const FuncionalidadesMap = function (){

	const init = function(){
		'use strict';
		generales();
		responsiveJsNoEquire();
		if (document.querySelector(".blackboxLoaderMapa")) {
			mapaFunction();
		}
		setTimeout(function(){

			

		}, 500);

	};

	function reportWindowSize() {
		setTimeout(function () {
			responsiveJsNoEquire();
		}, 500);
	}
	window.addEventListener('resize', reportWindowSize);

	const responsiveJsNoEquire = function () {
		let windowWidth = window.innerWidth;

		//	DESKTOP
		if (windowWidth >= 1151) {
			
		}
		//	TABLET LANDSCAPE
		else if (windowWidth >= 993 && windowWidth <= 1150) {
			
		}
		//	TABLET PORTRAIT 
		else if (windowWidth >= 768 && windowWidth <= 992) {

		}
		//	MOBILE
		else if (windowWidth <= 767) {

		}
	}

	const generales = function(){

		

	};

	//--------------------- FUNCIONALIDAD DEL MAPA ---------------------//

	const mapaFunction = function(){

		// ================ ITEM COLLECTED ===================== //

		let tlToDept = new TimelineMax();
		let tlOutDept = new TimelineMax();

		let currentBody = document.getElementById('mainContainer').getAttribute('dataclass');

		let svgObject = document.querySelectorAll("svg");
		let svgCountry = Snap(svgObject);

		// GENERAL BUTTONS
		let blackboxLoader = document.querySelector(".blackboxLoaderMapa");
		let backBtnColombia = document.querySelector(".backBtn");
		let moreTopicsBtn = document.querySelector(".btnMoreTopics");

		let selectDepto = document.querySelector('.contChoicheDpto');

		// CONTAINER REGIONES
		const regionesContainer = document.querySelector('.totalRegiones');

		// CONTAINER MAPA COLOMBIA
		const countryContainer = document.querySelector('.containerCountry');
		const countryMap = countryContainer.querySelector('.colombiaDepartmentsSvg');
		const countryMapInfo = countryContainer.querySelector('.informationContainerColombia');

		const departmentColPol = countryMap.querySelectorAll("polygon");
		const departmentColPath = countryMap.querySelectorAll("path");

		// GETTING THE RIGHT URL
		let urlNacional = 'https://mzzbmc3w8i.execute-api.us-east-1.amazonaws.com/default/ANHB?dep=0';
		let currentMap = document.getElementById('mainContainer').getAttribute('dataClass');

		// if (currentMap == 'mailMap') {
		// 	urlNacional = "/tables/general";
		// }
		// else if (currentMap == 'analiticaMap') { 
		// 	urlNacional = "/tables/general";
		// }

		// LLENADO DE CAMPOS NACIONAL
		let getNacional = function(url) {
      
      FuncionalidadesGenerales.getJSON(url, function(err, data) {
        if (err !== null) {
          console.log("Something went wrong: " + err);
        } else {
					// console.log(data);
          fillingSelect(data.Departamentos, selectDepto);

          [].forEach.call(departmentColPol, departmentPol => {
            let idDepartment = departmentPol.getAttribute("id");
            let codigoDane = idDepartment.split("-")[1];

            for (var i = 0; i < data.Departamentos.length; i++) {
              let listDpto = data.Departamentos[i];
							let nameDpto = listDpto.Departamento;
							
              if (listDpto.cod_dane_depto == codigoDane) {
                departmentPol.setAttribute("dataName", FuncionalidadesGenerales.titleCase(nameDpto, ' '));
              }
            }
          });
          [].forEach.call(departmentColPath, departmentPath => {
            let idDepartment = departmentPath.getAttribute("id");
            let codigoDane = idDepartment.split("-")[1];

            for (var i = 0; i < data.Departamentos.length; i++) {
              let listDpto = data.Departamentos[i];
							let nameDpto = listDpto.Departamento;
							
              if (listDpto.cod_dane_depto == codigoDane) {
                departmentPath.setAttribute("dataName", FuncionalidadesGenerales.titleCase(nameDpto , ' '));
              }
            }
          });

					fillingData(data);
					// FuncionalidadesGenerales.ajustarAltoNavegador(document.querySelector("header").clientWidth);
        }
      });
		};
		getNacional(urlNacional);

		// ================ HELPER FUNCTIONS ===================== //

		// FILLING DATA MAIL
		let fillingData = function (data) {
			/*==
				CAMPOS OBJ
			==*/
			// TOTAL MERCADOS
			let nameDptoCountry = countryMapInfo.querySelector("h1.nameCurrentStage"),
					mercadosEntregados = countryMapInfo.querySelector(".summaryContainer .total span.currentMercados"),
					mercadosMeta = countryMapInfo.querySelector(".summaryContainer .total span.totalMercados"),
					// TOTAL MERCADOS BY REGION
					listaRegiones = regionesContainer.querySelectorAll('ul li');

			// TOTAL MERCADOS
			let totalMercadosEntregados,
					totalMercadosMeta,
					// TOTAL MERCADOS BY REGION
					totalMercadosRegiones;
			if(data.Total){
				// TOTAL MERCADOS
				totalMercadosEntregados = parseInt(data.Total.Mercados_entregados);
				totalMercadosMeta = parseInt(data.Total.Meta);
				// TOTAL MERCADOS BY REGION
				totalMercadosRegiones = data.Regiones;
			}
			else{
				// TOTAL MERCADOS
				totalMercadosEntregados = parseInt(data.Mercados_entregados);
				totalMercadosMeta = parseInt(data.Meta);
			}


			/*==
				FILLING
			==*/
			if (data.Departamento) {
				nameDptoCountry.textContent = FuncionalidadesGenerales.titleCase(data.Departamento, " ");
			}
			else {
				nameDptoCountry.textContent = 'Nivel Nacional';
			}

			// TOTAL MERCADOS
			mercadosEntregados.innerHTML = FuncionalidadesGenerales.currencyFormatDE(totalMercadosEntregados);
			mercadosMeta.innerHTML = FuncionalidadesGenerales.currencyFormatDE(totalMercadosMeta);
			// TOTAL MERCADOS BY REGION
			if (totalMercadosRegiones) {
				listaRegiones.forEach((regionesToFill, i) => totalMercadosRegiones.forEach(totalRegiones => {
					let totalEntregados = regionesToFill.querySelector('.totalEntregado');
					let totalMeta = regionesToFill.querySelector('.totalMeta');
					if (regionesToFill.id == totalRegiones.cod_region) {
						totalEntregados.innerHTML = FuncionalidadesGenerales.currencyFormatDE(totalRegiones.Mercados_entregados);
						totalMeta.innerHTML = FuncionalidadesGenerales.currencyFormatDE(totalRegiones.Meta);
					}
				}))
			}

			blackboxLoader.style.display = "none";

		}

		
		// CHOICE DPTO AND MUNICIPIO THROUGH SELECT
		let chooseInSelect = function(selectTarget) {		

			selectTarget.addEventListener('input', function (event) {

				// CHOICING A DEPARTMENT

				// console.log(event.target.value);
				blackboxLoader.style.display = "flex";
				if (event.target.value !== '--') {
					getDpto(event.target.value);
				}
				else{
					for (let i = 0; i < departmentColPol.length; i++) {
            departmentColPol[i].classList.remove("selected");
          }
					for (let i = 0; i < departmentColPath.length; i++) {
            departmentColPath[i].classList.remove("selected");
					}
					backBtnColombia.style.display = 'none';
					getNacional(urlNacional);
				}

				if (moreTopicsBtn) {
					hidingMoreTopics(moreTopicsBtn);
				}

			});

		}

		// FILL SELECT MUNICIPIO
		let fillingSelect = function(listContentSelect, targetSelect) {

			let options;
			let selectTargetCode;

			if (targetSelect.classList.contains('contChoicheDpto')) {
				selectTargetCode = targetSelect.querySelector('.choicheDpto');
				
				options = '<option value="--">Selecciona un Departamento</option>';
				fillSelect(options, selectTargetCode);
			}

			function fillSelect(options, selectTargetCode) { 

				let optionsItem = options;
				let targetSelect = selectTargetCode;
				
				listContentSelect = listContentSelect.sort((a, b) => (a.Departamento > b.Departamento) ? 1 : -1);
	
				for (var i = 0; i < listContentSelect.length; i++) {
					let currentDpto = listContentSelect[i];
	
					optionsItem += '<option value="'+ currentDpto.cod_dane_depto +'">'+ currentDpto.Departamento +'</option>';
	
				}
	
				// console.log(targetSelect);
				// console.log(optionsItem);
				
				targetSelect.innerHTML = optionsItem;
				
				chooseInSelect(targetSelect);
			}


		}

		// HIDE MORE TOPICS
		let hidingMoreTopics = function (btnTopics) {
			btnTopics.classList.remove("btnLessTopics");
			btnTopics.parentNode.querySelector(".listTableByTopic").classList.remove("showAllTopics");
			btnTopics.parentNode.parentNode.classList.remove("showingAllTopics");
			btnTopics.parentNode.parentNode.parentNode.querySelector('.registersByCategory').classList.remove("hidingByTopics");
		}

		// Show an element
		let show = function (elem) {
			elem.style.display = 'block';
		};

		// Hide an element
		let hide = function (elem) {
			elem.style.display = 'none';
		};

		// Toggle element visibility
		let toggle = function (linkBtn) {
			for(var i = 0; i < linkBtn.length; i++){
				linkBtn[i].addEventListener('click', function(event){
					event.preventDefault();
					// let openTF = 0;

					let className = this.getAttribute('class');
					let currentBtn = className.split(' ')[0];
					// console.warn(currentBtn);
					let currentParent = this.parentNode;
					let othersTopics = FuncionalidadesGenerales.getSiblings(currentParent);
					let container = this.nextElementSibling;

					this.classList.toggle('activeTopic');

					let otherBtns = [];
					let topicToClose = [];
					let subTopicBtn = [];
					let subTopicToClose = [];

					for (var i = 0; i < othersTopics.length; i++) {

						otherBtns.push(othersTopics[i].firstElementChild)
						topicToClose.push(othersTopics[i].lastElementChild)
						subTopicBtn.push(othersTopics[i].querySelectorAll('.titleSubTopic'));
						subTopicToClose.push(othersTopics[i].querySelectorAll('.innerContListVitimsByCategory'));

					}

					if (currentBtn == 'titleSubTopic') {
						// console.warn('SUB TOPIC');
						// SUB TOPIC
						let parentSubTopic = container.closest(".topicContainer");

						if(container.classList.contains('active')){

							// console.log('Es visible');

							let tl = new TimelineMax();
							// console.warn('PASO DE ESTE PUNTO');

							tl
								.to(parentSubTopic, 0.1, {css:{height: 'auto'}, ease: Power2.easeOut})
								.to(container, 0.5, {height: 0, ease: Power2.easeOut})
								.set(container, {className:'-=active'});

						}
						else{

							// console.log('No es visible');

							let tl = new TimelineMax();
							// console.warn('PASO DE ESTE PUNTO');

							parentSubTopic.style.height = 'auto';

							container.classList.add('active');
							container.style.height = 'auto';
							let height = container.offsetHeight;
							container.style.height = '0px';


							tl
								.to(container, 0.5, {height: height, ease: Power2.easeOut})
								.staggerTo(topicToClose, 0.3, {height: 0, ease: Power2.easeOut}, 0, '-=0.3')
								.staggerTo(otherBtns, 0.3, {className: '-=activeTopic'}, 0, 0)
								.staggerTo(topicToClose, 0.01, {className: '-=active'}, 0);

						}

					}
					else{
						// console.warn('MAIN TOPIC');
						// MAIN TOPIC

						if(container.classList.contains('active')){

							// console.log('Es visible');

							let tl = new TimelineMax();
							// console.warn('PASO DE ESTE PUNTO');

							tl
								.to(container, 0.5, {height: 0, ease: Power2.easeOut})
								.set(container, {className:'-=active'})
								.staggerTo(container.querySelectorAll('.innerContListVitimsByCategory'), 0.3, {height: 0, ease: Power2.easeOut}, 0, '-=0.3')
								.staggerTo(container.querySelectorAll('.titleSubTopic'), 0.3, {className: '-=activeTopic'}, 0)
								.staggerTo(container.querySelectorAll('.innerContListVitimsByCategory'), 0.01, {className: '-=active'}, 0);

						}
						else{

							// console.log('No es visible');

							let tl = new TimelineMax();
							// console.warn('PASO DE ESTE PUNTO');

							container.classList.add('active');
							container.style.height = 'auto';
							let height = container.offsetHeight;
							container.style.height = '0px';

							tl
								.to(container, 0.5, {height: height, ease: Power2.easeOut})
								.staggerTo(topicToClose, 0.3, {height: 0, ease: Power2.easeOut}, 0, '-=0.3')
								.staggerTo(otherBtns, 0.3, {className: '-=activeTopic'}, 0, 0)
								.staggerTo(topicToClose, 0.01, {className: '-=active'}, 0)
								.staggerTo(subTopicToClose, 0.3, {height: 0, ease: Power2.easeOut}, 0, '-=0.3')
								.staggerTo(subTopicBtn, 0.3, {className: '-=activeTopic'}, 0)
								.staggerTo(subTopicToClose, 0.01, {className: '-=active'}, 0);

						}
					}

				});
			};
		};

		// ================ ACTION FUNTIONS ===================== //

		// CLICK DPTO
		let departmentClick = function(departmentCol) {

			let clickedDepartment

			if (departmentCol.length < 2) {

				clickedDepartment = countryMap.querySelector('[id*="-0'+ departmentCol +'"]');

			}
			else{

				clickedDepartment = countryMap.querySelector('[id*="-'+ departmentCol +'"]');

			}

			let siblingClickedElemet = FuncionalidadesGenerales.getSiblings(clickedDepartment);

			clickedDepartment.classList.remove('noSelected');
			clickedDepartment.classList.add('selected');

			for (let i = 0; i < siblingClickedElemet.length; i++) {
				siblingClickedElemet[i].classList.remove('selected');
				siblingClickedElemet[i].classList.add('noSelected');
			}

			backBtnColombia.style.display = 'flex';
			if (moreTopicsBtn) {
				hidingMoreTopics(moreTopicsBtn);
			}

		};

		// OVER AND TOOLTIL
		let cursorPositionIn = function(target, contentTooltip) {

			let tooltip = document.querySelector('.tooltipNames');
			let siblingsTarget = FuncionalidadesGenerales.getSiblings(target);

			show(tooltip);

			for (var i = 0; i < siblingsTarget.length; i++) {
				siblingsTarget[i].classList.remove('hoverIn');
			}
			target.classList.add('hoverIn');

			// Tracking Mouse Position
			target.parentElement.parentElement.parentElement.addEventListener('mousemove', function(mouseEvent) {

				let obj = this
				let obj_left = 0;
				let obj_top = 0;
				let xpos;
				let ypos;
				// console.warn(mouseEvent);
				// while (obj.offsetParent){
				// 	obj_left += obj.offsetLeft;
				// 	obj_top += obj.offsetTop;
				// 	obj = obj.offsetParent;
				// }
				if (mouseEvent){
					//FireFox
					xpos = mouseEvent.layerX;
					ypos = mouseEvent.layerY;
				}
				else{
					//IE
					xpos = window.event.x + document.body.scrollLeft - 2;
					ypos = window.event.y + document.body.scrollTop - 2;
				}

				xpos -= obj_left;
				ypos -= obj_top;



				tooltip.style.left = xpos + 15 + "px";
				tooltip.style.top = ypos + 15 + "px";

			});

			tooltip.getElementsByTagName('p')[0].innerHTML = contentTooltip;

		};
		let cursorPositionOut = function() {

			let tooltip = document.querySelector('.tooltipNames');
			let targetPol = document.querySelectorAll("polygon");
			let targetPath = document.querySelectorAll("path");

			hide(tooltip);

			for (var i = 0; i < targetPol.length; i++) {
				targetPol[i].classList.remove('hoverIn');
			}
			for (var i = 0; i < targetPath.length; i++) {
				targetPath[i].classList.remove('hoverIn');
			}

		};

		let getDpto = function getDpto(codigoDane) {
			let url ='https://mzzbmc3w8i.execute-api.us-east-1.amazonaws.com/default/ANHB?dep='+codigoDane;
      FuncionalidadesGenerales.getJSON(url, function(err, data) {
        if (err !== null) {
          console.log("Something went wrong: " + err);
        } else {
					// console.log(data);
					departmentClick(codigoDane);
					fillingData(data);
        }
      });
    };

		// ================ ACTIVATION FUNCTIONS ===================== //

		// DEPATMENT ACTIONS
		[].forEach.call( departmentColPol, departmentPol => {

			let idDepartment = departmentPol.getAttribute("id");
			let codigoDane = idDepartment.split("-")[1];

			departmentPol.addEventListener('click', function() {

				// console.log("Click Btn")
				if (codigoDane[0] == 0) {
					selectDepto.querySelector('select').value = codigoDane[1];
				}
				else{
					selectDepto.querySelector('select').value = codigoDane;
				}
				// selectDepto.querySelector('select').value = codigoDane;
				blackboxLoader.style.display = "flex";
				getDpto(codigoDane);

			});
			departmentPol.addEventListener("mouseover", function() {

				let namesDepartment = departmentPol.getAttribute("dataName");
				cursorPositionIn(departmentPol, namesDepartment);

				// selectDepto.querySelector('select').value = codigoDane;
				if (codigoDane[0] == 0) {
					selectDepto.querySelector('select').value = codigoDane[1];
				}
				else {
					selectDepto.querySelector('select').value = codigoDane;
				}
				// blackboxLoader.style.display = "flex";
				getDpto(codigoDane);
				
			});
			departmentPol.addEventListener("mouseout", function() {
				cursorPositionOut();
			});

		});
		
		[].forEach.call( departmentColPath, departmentPath => {

			let idDepartment = departmentPath.getAttribute("id");
			let codigoDane = idDepartment.split("-")[1];

			departmentPath.addEventListener('click', function() {

				// console.log("Click Btn")
				if (codigoDane[0] == 0) {
					selectDepto.querySelector('select').value = codigoDane[1];
				}
				else{
					selectDepto.querySelector('select').value = codigoDane;
				}
				// selectDepto.querySelector('select').value = codigoDane;
				blackboxLoader.style.display = "flex";
				getDpto(codigoDane);

			});
			departmentPath.addEventListener("mouseover", function() {

				let namesDepartment = departmentPath.getAttribute("dataName");
				cursorPositionIn(departmentPath, namesDepartment);

				// selectDepto.querySelector('select').value = codigoDane;
				if (codigoDane[0] == 0) {
					selectDepto.querySelector('select').value = codigoDane[1];
				}
				else {
					selectDepto.querySelector('select').value = codigoDane;
				}
				// blackboxLoader.style.display = "flex";
				getDpto(codigoDane);

			});
			departmentPath.addEventListener("mouseout", function() {
				cursorPositionOut();
			});

		});

		// SHOW TOPICS & INFORMATION
		toggle(document.querySelectorAll('.titleTopic'));
		toggle(document.querySelectorAll('.titleSubTopic'));

		// BACK BUTTON COLOMBIA
		backBtnColombia.addEventListener('click', function() {

			let colPol = document.querySelectorAll("polygon");
			let colPath = document.querySelectorAll("path");

			this.style.display = 'none';

			for (let i = 0; i < colPol.length; i++) {
				colPol[i].classList.remove('selected');
			}
			for (let i = 0; i < colPath.length; i++) {
				colPath[i].classList.remove('selected');
			}

			selectDepto.querySelector('select').value = '--';

			blackboxLoader.style.display = "flex";
			getNacional(urlNacional);

			if (moreTopicsBtn) {
				hidingMoreTopics(moreTopicsBtn);
			}

		});

		// SHOW ALL TOPICS
		if (moreTopicsBtn) {
			moreTopicsBtn.addEventListener("click", function () {
				this.classList.toggle("btnLessTopics");
				this.parentNode.querySelector(".listTableByTopic").classList.toggle("showAllTopics");
				this.parentNode.parentNode.classList.toggle("showingAllTopics");
				this.parentNode.parentNode.parentNode.querySelector('.registersByCategory').classList.toggle("hidingByTopics");
			});
		}

	};

	return{
		init:init,
	}
}();

